(function() {
  'use strict';

  angular
    .module('app.layout')
    .controller('LayoutController', LayoutController);

  LayoutController.$inject = [
                            'sectionsService',
                            'sidenavService',
                            '$timeout',
                            'webDevTec',
                            'toastr'];
  /** @ngInject */
  function LayoutController(
                            sectionsService,
                            sidenavService,
                            $timeout,
                            webDevTec,
                            toastr) {
    /*jshint validthis: true*/
    var vm = this;
    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.toggleSideNav   = sidenavService.toggleList;
    vm.openLeftMenu = sidenavService.openLeftMenu;

    vm.user = {};

    vm.selected = sectionsService.selected;
    vm.select = select;

    activate();

    function select (section) {
                    sectionsService.selectSection(section);
      vm.selected = sectionsService.selected;

      vm.toggleSideNav();
    }

    function activate() {
      console.log('LayoutController connected');

      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);

      // vm.selected = sectionsService.content[0];
    }

    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('healthGuide')
    .config(routerConfig);

  function routerConfig($stateProvider,
                        $urlRouterProvider
                        ) {
    $stateProvider
    //DRY
      .state('home', {
        url: '/',
        templateUrl: 'app/app.html',
        controller: 'LayoutController',
        controllerAs: 'layout'
      })
      .state('Nutrition', {
        url: '/nutrition',
        templateUrl: 'app/templates/nutrition.html',
        controller: 'LayoutController',
        controllerAs: 'layout'
      })
      .state('Sleep', {
        url: '/sleep',
        templateUrl: 'app/templates/sleep.html',
        controller: 'LayoutController',
        controllerAs: 'layout'
      })
      .state('Activity', {
        url: '/activity',
        templateUrl: 'app/app.html',
        controller: 'LayoutController',
        controllerAs: 'layout'
      })
      .state('Your health', {
        url: '/way_to_healthy_life',
        templateUrl: 'app/app.html',
        controller: 'LayoutController',
        controllerAs: 'layout'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

(function() {
  'use strict';

  angular
    .module('healthGuide')
    .config(config)
    .run(runBlock)
// constants used by entire app
    .constant('malarkey', malarkey)
    .constant('moment', moment)

// constants used by specific module
  angular
    .module('app.layout')
    .constant('malarkey', malarkey);

  /** @ngInject */
  function config($logProvider,
                  toastrConfig,
                  $mdIconProvider,
                  $mdThemingProvider
                  ) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;

    $mdIconProvider
      .defaultIconSet("../assets/svg/avatars.svg", 128)
      .icon("menu"       , "../assets/svg/menu.svg"        , 24)
      .icon("share"      , "../assets/svg/share.svg"       , 24)
      .icon("google_plus", "../assets/svg/google_plus.svg" , 512)
      .icon("hangouts"   , "../assets/svg/hangouts.svg"    , 512)
      .icon("twitter"    , "../assets/svg/twitter.svg"     , 512)
      .icon("phone"      , "../assets/svg/phone.svg"       , 512)
      .icon("angular"    , "../assets/images/angular.png"  , 512);

    $mdThemingProvider.theme('default')
        .primaryPalette('blue-grey')
        .accentPalette('blue', {
            default: '400'
        });
  }


  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }
})();

